/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
@font-face {
  font-family: 'bernier';
  src: url("../../fonts/BERNIERRegular-Regular.otf");
  font-weight: normal;
  font-style: normal; }

body {
  -webkit-font-smoothing: antialiased; }

body,
button,
input,
select,
textarea {
  color: #373e42;
  font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.6; }
  @media only screen and (max-width: 480px) {
    body,
    button,
    input,
    select,
    textarea {
      font-size: 20px;
      font-size: 1.25rem; } }

h1, h2, h3, h4, h5, h6 {
  margin-top: .75em;
  font-family: "Roboto", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
  margin-bottom: .25em;
  clear: both;
  font-weight: normal; }

h1 {
  font-size: 3em; }

h2 {
  font-size: 2.6em; }

h3 {
  font-size: 2.2em; }

h4 {
  font-size: 2em; }

h5 {
  font-size: 1.8em; }

h6 {
  font-size: 1.6em; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Setup
--------------------------------------------------------------*/
html {
  height: 100%; }

body {
  height: 100%;
  margin: 0; }

* {
  box-sizing: border-box; }

h1, h2, h3, h4, h5 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: normal; }

ul {
  padding: 0;
  margin: 0;
  list-style: none; }

p {
  margin: 0; }

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.wrapper {
  min-height: 100%;
  position: relative;
  overflow: hidden; }

/*--------------------------------------------------------------
## Blocks
--------------------------------------------------------------*/
.triangle {
  width: 100%;
  height: 100px; }
  .triangle_fill_white {
    fill: #efeee9; }
  .triangle_fill_beige {
    fill: #eeebdf; }

.title {
  color: #373e42;
  font-family: 'bernier';
  font-size: 60px;
  font-size: 3.75rem;
  padding: .75em 1.333em;
  display: inline-block;
  position: relative;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat; }
  .title:after, .title:before {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(55, 62, 66, 0.2);
    width: 22px;
    height: 5px; }
  .title:after {
    right: 0.667em; }
  .title:before {
    left: 0.667em; }

/*--------------------------------------------------------------
## Hero
--------------------------------------------------------------*/
.hero {
  height: 600px;
  overflow: hidden;
  position: relative; }
  .hero__img-bg {
    width: 100%;
    min-height: 880px;
    min-width: 1800px; }
  .hero__title {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    width: 100%; }
  .hero__user-block {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.section-title {
  display: inline-block; }

.down-arrow {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 40px; }

/*--------------------------------------------------------------
## About
--------------------------------------------------------------*/
.about__left, .about__right {
  position: relative;
  float: left;
  width: 50%; }

.about__left {
  background: #efeee9; }

.about__right {
  background: #eeebdf; }

.about__triangles {
  position: absolute;
  bottom: 100%; }

